<template>
  <div id="searchfailed">
    <head-top title="Add Devices" goBack = 'true'></head-top>
    <p>Failed to find any plug. Please make sure the  plug  is in the same LAN，then retry</p>
    <img src="../../../assets/addevice/failure@3x.png" alt="">
    <div class="button" @click="$router.go(-1)">
      Retry
    </div>
  </div>
</template>
<script>
import headTop from '../../../components/common/head'

export default {
  data () {
    return {}
  },
  components: {
    headTop
  }
}
</script>
<style lang="less" scoped>
#searchfailed{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  p{
    padding: 95px 55px 0 55px;
    font-size: 33px;
    line-height: 43px;
    color: #595959;
    text-align: left;
  }
  img{
    .wh(210px, 279px);
    align-self: center;
    margin:105px 0 233px 0;
  }
}
</style>



